html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: sftext;
  src: url("../fonts/sf-pro-text_regular.woff2") format("woff2"), url("../fonts/sf-pro-text_regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: semibold;
  src: url("../fonts/sf-pro-display_semibold.woff2") format("woff2"), url("../fonts/sf-pro-display_semibold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

canvas {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}

body {
  background: #fff;
  font-family: sftext,sans-serif;
  font-synthesis: none;
  -moz-font-feature-settings: 'kern';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  max-width: 980px;
  margin: 0 auto;
}

.section {
  height: 100vh;
  width: 100%;
  position: relative;
  height: 240vh;
}

.iphone {
  position: absolute;
  width: 720px;
  height: 1444px;
  left: 50%;
  top: -300px;
  margin-left: -360px;
}

.iphone__frame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../img/iphone.png) no-repeat 0 0;
  background-size: 720px 1444px;
  z-index: 2;
}

.iphone__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: url("../img/fallback.jpg") no-repeat 0 0;
  background-size: cover;
}

.iphone__video video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header {
  height: 44px;
  position: absolute;
  left: 0;
  width: 100%;
  background: #333333;
  line-height: 44px;
  text-align: center;
  font-size: 14px;
  z-index: 1000;
}

.header a {
  text-decoration: none;
  color: #fff;
  margin: 0 30px;
}

.subheader {
  border-bottom: 1px solid #D6D6D6;
  height: 52px;
  position: absolute;
  top: 44px;
  left: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.9);
  width: 100%;
  padding-top: 10px;
}

.subheader .container {
  display: flex;
  align-items: baseline;
}

.subheader__nav {
  margin-left: auto;
}

.subheader__logo {
  font-size: 24px;
}

.subheader nav {
  display: inline-block;
  font-size: 12px;
}

.subheader nav a {
  margin-right: 24px;
  font-size: 12px;
  color: #333;
  text-decoration: none;
}

.button {
  background: linear-gradient(#42a1ec, #0070c9);
  border-color: #07c;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 12px;
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0em;
  min-width: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 1px;
  padding-bottom: 1px;
  text-decoration: none;
}

.next {
  text-align: center;
  padding: 100px 0;
}

.next h1 {
  font-size: 80px;
  line-height: 1.05;
  font-weight: 600;
  letter-spacing: -.015em;
  font-family: semibold,sans-serif;
}

.next p {
  font-size: 32px;
  line-height: 1.25;
  font-weight: 600;
  letter-spacing: .004em;
  font-family: semibold,sans-serif;
  margin-top: 20px;
  max-width: 800px;
  margin: 20px auto 0 auto;
}

.next_gray {
  background: #fafafa;
}
